html {
  --header-height: 60px;
}

body {
  background-color: #fff;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  line-height: var(--header-height);
  height: var(--header-height);
  background-color: #fff;
  justify-content: space-between;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 10px 50px;
  display: flex;
}

.nav {
  background-color: #fff;
  margin-right: 50px;
}

.nav__list {
  gap: 20px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  color: #000;
  text-decoration: none;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid #00acdc;
  border-radius: 8px;
  width: 100%;
  display: block;
  position: relative;
  top: -4px;
  left: 0;
}
